/**
 *
 * Heading Styles
 *
 * @author Takuto Yanagida @ Space-Time Inc.
 * @version 2018-05-06
 *
 */


$stile-heading-color-text: rgba(#000, 0.95) !default;


@mixin stile-heading($set-design: true) {

	h1, h2, h3, h4, h5, h6 {
		@include font-proportional;
		hyphens: auto;  // Autoprefixer is needed. 

		-ms-word-break: break-all;
		word-break: break-word;
		word-wrap: break-word;
		&:target:focus { outline: none; }

		@if $set-design { color: $stile-heading-color-text; }

		> small { display: block; }
	}

	h1 { @include font-size-level(6); }
	h1[data-stile ~= 'small'], h1 > small { @include font-size-level(5); }
	h1[data-stile ~= 'small'] > small { @include font-size-level(4); }

	h2 { @include font-size-level(5); }
	h2[data-stile ~= 'small'], h2 > small { @include font-size-level(4); }
	h2[data-stile ~= 'small'] > small { @include font-size-level(3); }

	h3 { @include font-size-level(4); }
	h3[data-stile ~= 'small'], h3 > small { @include font-size-level(3); }
	h3[data-stile ~= 'small'] > small { @include font-size-level(2); }

	h4 { @include font-size-level(3); }
	h4[data-stile ~= 'small'], h4 > small { @include font-size-level(2); }
	h4[data-stile ~= 'small'] > small { @include font-size-level(1); }

	h5 { @include font-size-level(2); }
	h5[data-stile ~= 'small'], h5 > small { @include font-size-level(1); }

	h6 { @include font-size-level(1); }

}
